@import url(https://fonts.googleapis.com/css?family=Baloo+Da+2|Ubuntu:300&display=swap);
*{box-sizing:border-box}html{font-size:62.5%}body{font-family:'Ubuntu', sans-serif;font-size:1.8rem;background:oldlace;color:#000;max-width:100vw}h1,h2,h3,h4,h5,h6{font-family:'Baloo Da 2', cursive}.button{padding:10px 20px;background:seagreen;color:oldlace;border:none;border-radius:20px;transition:all .2s ease-in}.button:hover{cursor:pointer;background:mediumseagreen}.button:focus{outline:none}.button:active{transform:scale(0.95)}.header{text-align:center}span{font-weight:bold}.wrapper{width:90%;margin:40px auto;padding:15px}.meal-container{display:flex;flex-direction:row-reverse}.meal__img{width:400px;border-radius:10px;height:auto;margin-right:30px}.meal__info{margin-top:-20px;font-family:'Ubuntu', sans-serif}@-webkit-keyframes fade-in{from{transform:translateX(-3000px)}to{transform:translateX(0px)}}@keyframes fade-in{from{transform:translateX(-3000px)}to{transform:translateX(0px)}}.meal-container ol{padding:16px}iframe.meal-video{width:100%;height:400px}@media screen and (max-width: 768px){.meal__img{max-width:45rem;width:100%;border-radius:30px;display:block;height:auto;margin:30px auto}iframe.meal-video{width:100%;height:300px;margin-bottom:100px}.meal-container{display:flex;flex-direction:column-reverse}}.footer{text-align:center;font-size:1.4rem;height:100%}

